* {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  box-sizing: border-box;
  margin: 0;
}

body.dark {
  background-color: #292C35;
}
body.dark .text--big {
  color: #fff;
}
body.dark .text--small {
  color: #8b97c6;
}
body.dark .infobox {
  background-color: var(--dark-bg);
}
body.dark .infobox__today__grid {
  background-color: var(--dark-bg);
}
body.dark .footer {
  color: #fff;
}

.footer {
  margin-top: 2%;
  text-align: center;
}
.footer a {
  text-decoration: var(--light-text1);
}

:root {
  --font-inter: "Inter",sans-serif;
  --limegreen: #1db489;
  --brightred: #dc414c;
  --facebook: #198ff5;
  --twitter: #1ca0f2;
  --instagram-start: #fdc468;
  --instagram-middle: #ee867c;
  --instagram-end: #df4996;
  --youtube: #c4032a;
  --toggle-bg-light: #aeb3cb;
  --toggle-bg-start: #378fe6;
  --toggle-bg-end: #3eda82;
  --toggle-light: #868cac;
  --toggle-button-light: #f0f2f9;
  --dark-bg: #1e202a;
  --dark-top-bg: #1f212e;
  --dark-card: #252a41;
  --dark-card-hover: #343b56;
  --dark-text1: #8b97c6;
  --dark-text2: #fff;
  --light-bg: #fff;
  --light-top-bg: #f5f7ff;
  --light-card: #f0f2fa;
  --light-card-hover: #e0e4f0;
  --light-text1: #63677e;
  --light-text2: #1e202a;
  --background: var(--light-bg);
  --text-color: var(--light-text2);
  --text-color2: var(--light-text1);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
  --toggle-bg: linear-gradient(225deg,var(--toggle-bg-end) 0%,var(--toggle-bg-start) 98.02%);
}

.header {
  display: flex;
  justify-content: space-between;
}
@media screen and (max-width: 1140px) {
  .header .title {
    font-size: 0.9375rem;
  }
}
@media screen and (max-width: 750px) {
  .header {
    flex-wrap: wrap;
    justify-content: normal;
  }
  .header .title {
    width: 100%;
    font-size: 0.625rem;
  }
}

.info__top {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  justify-items: center;
}
@media screen and (max-width: 1140px) {
  .info__top {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 750px) {
  .info__top {
    grid-template-columns: auto;
  }
}
.info__top .infobox {
  height: 17.5rem;
  width: 85%;
  background-color: var(--card-bg);
  border-radius: 0.3125rem;
  margin-top: 3.125rem;
}
.info__top .infobox__platfrom {
  margin-top: 1.25rem;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 1.875rem;
}
.info__top .infobox__platfrom img {
  width: 10%;
}
.info__top .infobox__platfrom__username {
  margin-left: 0.625rem;
}
.info__top .infobox__followers {
  margin: 1.875rem auto;
  text-align: center;
}
.info__top .infobox__followers__count--big {
  font-size: 3.75rem;
}
.info__top .infobox__followers__label {
  font-size: 1.875rem;
  letter-spacing: 0.3125rem;
}
.info__top .infobox__change {
  display: flex;
  align-items: center;
  justify-content: center;
}
.info__top .infobox__change .number__change {
  margin-left: 0.3125rem;
}
.info__top .infobox:hover {
  background-color: var(--card-hover);
}
.info__top--facebook {
  border-top: var(--facebook) 0.3125rem solid;
}
.info__top--twitter {
  border-top: var(--twitter) 0.3125rem solid;
}
.info__top--instagram {
  border-top: linear-gradient(225deg, var(--instagram-end), var(--instagram-middle) 50.91%, var(--instagram-start) 100%) 0.3125rem solid;
}
.info__top--youtube {
  border-top: var(--youtube) 0.3125rem solid;
}

.info__today__overview {
  margin: 1.875rem 0;
}
.info__today .infobox__today {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  gap: 1.25rem;
}
@media screen and (max-width: 1140px) {
  .info__today .infobox__today {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media screen and (max-width: 750px) {
  .info__today .infobox__today {
    grid-template-columns: auto;
  }
}
.info__today .infobox__today__grid {
  display: grid;
  height: 9.375rem;
  background-color: var(--card-bg);
  border-radius: 0.3125rem;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  gap: 0.3125rem;
  padding: 1.875rem;
  justify-items: start;
}
.info__today .infobox__today__grid__sub {
  font-weight: 600;
  font-size: 1.125rem;
}
.info__today .infobox__today__grid__img {
  justify-self: end;
}
.info__today .infobox__today__grid__count--small {
  font-size: 1.875rem;
  align-self: end;
}
.info__today .infobox__today__grid__change {
  align-self: end;
  justify-self: end;
  display: flex;
  align-items: center;
  justify-content: center;
}
.info__today .infobox__today__grid__change .number__change {
  margin-left: 0.3125rem;
}
.info__today .infobox__today__grid:hover {
  background-color: var(--card-hover);
}

.toggle-container {
  margin-top: 1.875rem;
  display: flex;
}
.toggle-container .text {
  margin-right: 0.625rem;
  margin-top: 0.3125rem;
}
.toggle-container .checkbox {
  opacity: 0;
  position: absolute;
}
.toggle-container .checkbox:checked + .label {
  background-color: var(--toggle-bg);
}
.toggle-container .label {
  background-color: #aeb3cb;
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 0.625rem;
  width: 3.75rem;
  height: 1.875rem;
  position: relative;
}
.toggle-container .ball {
  background-color: #fff;
  height: 1.875rem;
  width: 1.75rem;
  position: absolute;
  border-radius: 50%;
  transition: transform 0.2s linear;
}
.toggle-container .checkbox:checked + .label .ball {
  transform: translateX(1.875rem);
}

.text--big {
  color: var(--text-color);
  font-weight: 800;
}

.text--small {
  color: var(--text-color2);
}

.mini-title {
  font-weight: 600;
  font-size: 1.25rem;
}

.infobox__change--up {
  color: #1db489;
  font-weight: 600;
}

.infobox__change--down {
  color: #dc414c;
  font-weight: 600;
}